import React, { useEffect, useState } from 'react';
import './SequenceGame.scss';
import TypeWriter from '../components/TypeWriter';
import KeyPress from '../components/KeyPress';
import EnterSVG from '../assets/enter.svg';
import Deny from '../assets/deny.mp3';
import ReactPlayer from 'react-player';
const denyAudio = new Audio(Deny)

function SequenceGame({ back, next, config, setStartTimer, setResetTimer }) {
  const { style, audio, contents, answer, length, video } = config;
  const [showHint, setShowHint] = useState(false);
  const [showVideo, setShowVideo] = useState(false);
  const [showHelp, setShowHelp] = useState(false);
  const [value, setValue] = useState('');
  useEffect(() => {
    if (audio) {
      audio.play();
    }
    return () => {
      if (audio) {
        audio.pause();
      }
    }
  }, [audio])

  /*useEffect(() => {
    console.log('useeffect config change')
    setShowHelp(false)
  }, [config])
*/
  useEffect(() => {
  }, [value])

  const onDone = () => {
    console.log('CodeGeneric Done')
    setShowHelp(true);
    setStartTimer(true);
  };

  const onKeyDown = (key) => {
    if (key === 'Shift' && showHelp && !showHint) {
      setShowHint(!showHint)
    }
    if (key === 'Shift' && showHelp && showHint) {
      setShowVideo(true)
    }
    if (key === 'Enter' && !showHint) {
      if (value.length === length) {
        if (answer === value) {
          next()
        } else {
          denyAudio.play()
          setValue('')
          setResetTimer(value => value + 1)
        }
      }

    }
    if (key === 'Backspace') {
      setValue((value) => {
        return value.slice(0, -1)
      })
    }

    if (['0', '1', '2', '3', '4', '5'].includes(key) && !value.includes(key)) {
      setValue((value) => {
        if (length > 0 && length > value.length) {
          return value + key.toString()
        }
        return value;

      });
    }
  }
  console.log('CodeGeneric showHelp', showHelp)
  const styleBox = {
    display: 'inline-block',
    marginLeft: '10px',
    marginRight: '10px',
    textAlign: 'center',
    fontSize: '26px'
  }
  return (
    <div className="Screen sequence">
      <div className="content">

        { !value.includes('1') && <div style={styleBox}>
          {mountain()}
          <p>1</p>
        </div>}
        { !value.includes('2') &&<div style={styleBox}>
          {buildings()}
          <p>2</p>
        </div>}
        { !value.includes('3') &&<div style={styleBox}>
          {tree()}
          <p>3</p>
        </div>}
        { !value.includes('4') &&<div style={styleBox}>
          {woods()}
          <p>4</p>
        </div>}
        { !value.includes('5') &&<div style={styleBox}>
          {beach()}
          <p>5</p>
        </div>}
        
      </div>
      <div className="interactions">
        <TypeWriter contents={contents}
          speed={100}
          cursor={false}
          onDone={onDone} />
        {showHelp &&
          <>
            {value.split("").map((character, index) => {
              if(character === '1') {
                return <div key={index} className="small-icon">
                  {mountain()}
                </div>
              } else if(character === '2') {
                return <div key={index} className="small-icon">
                {buildings()}
              </div>
              } else if(character === '3') {
                return <div key={index} className="small-icon">
                {tree()}
              </div>
              } else if(character === '4') {
                return <div key={index} className="small-icon">
                {woods()}
              </div>
              } else if(character === '5') {
                return <div key={index} className="small-icon">
                {beach()}
              </div>
              }
            })}
  
            {value.length === length &&
              <img src={EnterSVG} className="enter blink"></img>
            }

            <KeyPress onKeyDown={onKeyDown}>
              <p className="hint">press SHIFT to rewatch footage</p>
            </KeyPress>

           
          </>
        }



      </div>
      {showHint &&
        <div className={`${!showVideo ? 'hint2' : 'hint-container'}`}>
          {showVideo && <ReactPlayer url={video} style={style} playing={true} onEnded={() => { setShowHint(false);setShowVideo(false) }} controls={false}></ReactPlayer>}
          {!showVideo && <p >Urgh! I knew you were distracted while the footage was playing.<br/><br/>
                  You’re wasting time!
                  press SHIFT to rewatch footage</p>}
         
        </div>
      }
    </div>
  );
}

export default SequenceGame;

const mountain = () => {
  return <img id="image0_42_7" width="331" height="278" src="data:image/png;base64,"/>
}
const buildings = () => {
  return <img id="image0_42_3" width="320" height="268" src="data:image/png;base64,"/>
}
const tree = () => {
 return <img id="image0_42_11" width="424" height="405" src="data:image/png;base64,"/>
}

const woods = () => {
  return <img id="image0_42_9" width="294" height="267" src="data:image/png;base64,"/>
}
const beach = () => {
  return <img id="image0_42_5" width="665" height="519" src="data:image/png;base64,"/>
}
