import React, { useEffect, useState } from 'react';
import './DirectionsGame.scss';
import TypeWriter from '../components/TypeWriter';
import KeyPress from '../components/KeyPress';
import EnterSVG from '../assets/enter.svg';
import Deny from '../assets/wrong.mp3';

import Left from '../assets/left.png';
import Right from '../assets/right.png';
import Up from '../assets/up.png';
import Down from '../assets/down.png';
import LeftGif from '../assets/left.gif';
import RightGif from '../assets/right.gif';
import UpDownGif from '../assets/up_down.gif';
import EndGif from '../assets/end.gif';
import DenyGif from '../assets/wrong.gif';

const denyAudio = new Audio(Deny)

function DirectionsGame({ next, config, setStartTimer, setResetTimer }) {
  const { image, style, audio, contents, answer, length, hint } = config;
  const [showHint, setShowHint] = useState(false);
  const [animating, setAnimating] = useState(false);
  const [gif, setGif] = useState(null);
  const [showHelp, setShowHelp] = useState(false);
  const [hintUsed, setHintUsed] = useState(false);
  const [value, setValue] = useState('');
  useEffect(() => {
    if (audio) {
      audio.play();
    }
  }, [audio])

  /*useEffect(() => {
    console.log('useeffect config change')
    setShowHelp(false)
  }, [config])
*/
  useEffect(() => {
  }, [value])

  const onDone = () => {
    console.log('CodeGeneric Done')
    setShowHelp(true);
    setStartTimer(true);
  };

  const onKeyDown = (key) => {
    console.log(key);
    if (key === 'm') {
      setShowHint(!showHint)
    }
    if (key === 'Enter') {
      if (value.length === length) {
        if (answer === value) {

          setAnimating(true);
          setGif(EndGif)

          setTimeout(() => {
            if(audio) audio.pause();
            next()
          }, 4000)
          
        } else {
          setGif(DenyGif)
          denyAudio.play()
          setValue('')
          setResetTimer(value => value + 1)
        }
      }

    }
    if (key === 'Backspace') {
      setValue((value) => {
        return value.slice(0, -1)
      })
    }

    if (['ArrowUp', 'ArrowDown', 'ArrowLeft', 'ArrowRight'].includes(key) && !animating) {
      setValue((value) => {
        if (length > 0 && length > value.length) {
          setAnimating(true);
          if(key === 'ArrowLeft') {setGif(LeftGif)}
          if(key === 'ArrowRight') {setGif(RightGif)}
          if(key === 'ArrowUp') {setGif(UpDownGif)}
          if(key === 'ArrowDown') {setGif(UpDownGif)}

          return value + key.replace('ArrowUp','U').replace('ArrowDown','D').replace('ArrowLeft','L').replace('ArrowRight','R')
        }
        return value;

      });
    }
  }

  useEffect(() => {
    if(gif !== null && gif !== EndGif ) {
      setTimeout(() => {
        setGif(null);
        setAnimating(false);
        if(value.length === length) {
          if (answer === value) {

            setAnimating(true);
            setGif(EndGif)
  
            setTimeout(() => {
              if(audio) audio.pause();
              next()
            }, 4000)
            
          } else {
            setGif(DenyGif)
            denyAudio.play()
            setValue('')
            setResetTimer(value => value + 1)
          }
        }
      }, 4000)
    }

  }, [gif])
  console.log('CodeGeneric value', value)
  return (
    <div className="Screen code">
      <div className="content">
        <img src={ gif ? gif : image} style={style} />
      </div>
      <div className="interactions">
        <TypeWriter contents={contents}
          speed={100}
          cursor={false}
          onDone={onDone} />
        {showHelp &&
          <>
            <p className="input">{value.split("").map((character, index) => {
              if(character === 'U') {
                return <img key={index} style={{marginRight: '4px'}} src={Up}/>
              } else if(character === 'D') {
                return <img key={index} style={{marginRight: '4px'}} src={Down}/>
              } else if(character === 'L') {
                return <img key={index} style={{marginRight: '4px'}} src={Left}/>
                
              } else if(character === 'R') {
                return <img key={index} style={{marginRight: '4px'}} src={Right}/>
              }
            })}</p>
          

            {value.length === length && !animating &&
              <img src={EnterSVG} className="enter blink"></img>
            }

            <KeyPress onKeyDown={onKeyDown}>
              <p className="hint">press M to see the map again</p>
            </KeyPress>
          </>
        }

      </div>
      {showHint &&
        <div className="hint-container" >
          <img style={{position: 'relative', marginBottom: '22px'}} id="image0_78_20" height="500" src="data:image/png;base64,"/>
          <p className="close" style={{marginTop:'20px'}}>press M to Close</p>
        </div>
      }
    </div>
  );
}

export default DirectionsGame;
